.profile{grid-area:profile-content;display:grid;grid-template-columns:270px 1fr;grid-template-rows:270px 1fr;grid-template-areas:'userpic about' 'blank posts';grid-gap:20px;gap:20px;height:100%}.profile__spinner{position:absolute;top:50%;left:50%}.profile__userpic{grid-area:userpic;display:flex;justify-content:center;align-items:center;width:270px;height:270px;background-color:#fff;box-shadow:0px 1px 15px 0px rgba(51,51,51,0.2)}.profile__about{grid-area:about;width:100%}.profile__my-posts{grid-area:posts}

.my-posts__add-post{margin-bottom:40px}

