.addPost-form{display:flex;width:100%;align-items:center}.addPost-form__field{display:block;width:100%;height:24px;background-color:#f1f1f1;border:none;resize:none;outline:none;overflow:hidden}.addPost-form__field::-webkit-input-placeholder{text-transform:capitalize}.addPost-form__field:-ms-input-placeholder{text-transform:capitalize}.addPost-form__field::-ms-input-placeholder{text-transform:capitalize}.addPost-form__field::placeholder{text-transform:capitalize}.addPost-form__button{width:100px}

.about{padding:25px;width:100%;min-height:270px;background-color:#fff;box-shadow:0px 1px 15px 0px rgba(51,51,51,0.2)}.about__name{margin:0;margin-bottom:5px;padding:0;font-size:19px;line-height:25px;font-weight:700;color:#333333;text-transform:capitalize}.about__status{position:relative;margin:0;padding:0;margin-bottom:30px}.about__status::after{bottom:-12px;left:0;width:30px;height:1px;content:"";position:absolute;background-color:#dc4734}.about__info-title{color:#333333;font-weight:700}.about__work{margin-bottom:20px}

