.error{border:2px solid red}.error-message{color:red;font-weight:700}

.users{padding-left:20px;max-width:42rem}.users__item{margin-bottom:20px}.users__item:last-of-type{margin-bottom:0}.users__list{margin:0;padding:0;list-style:none}.users__pagination{display:flex;justify-content:center;margin-top:1rem;margin-bottom:2rem}

